import React, { ReactNode, useMemo } from 'react';
import classNames from 'classnames/bind';
import {
  ShareableButtonProps,
  ButtonExternalType,
  ButtonExternalSize,
  ButtonCoreType,
  ButtonCoreSize,
  ButtonBaseStyles
} from 'shared/types/button';

type ButtonTags = 'button' | 'a';

export const DEFAULT_PROPS = {
  rel: 'nofollow',
  role: 'button',
  ariaLabel: 'Кнопка',
  target: '_blank',
  disabled: false
};

export interface BaseButtonProps extends ShareableButtonProps {
  /** Стили  кнопки */
  styles: ButtonBaseStyles;

  /** Тип кнопки */
  type?: ButtonExternalType | ButtonCoreType;

  /** Размер кнопки */
  size?: ButtonExternalSize | ButtonCoreSize;

  /** Контент кнопки */
  content: string | React.ReactElement;

  /** Пропс для растягивания кнопки на ширину родительского контейнера */
  stretch?: boolean;

  /** Флаг, акцентное отображение кнопки для Secondary и Tertairy варианта отображения(только External) */
  accent?: boolean;

  /** Флаг траурного режима */
  isMourning?: boolean;

  /** Функция принимающая поле size и возвращаюшая loader */
  renderLoader?: (arg: ButtonExternalSize | ButtonCoreSize | undefined) => ReactNode;
}

export const BaseButton = ({
  onClick,
  type,
  content,
  href,
  rel = DEFAULT_PROPS.rel,
  role = DEFAULT_PROPS.role,
  target = DEFAULT_PROPS.target,
  ariaLabel = DEFAULT_PROPS.ariaLabel,
  disabled = DEFAULT_PROPS.disabled,
  dataTestId,
  styles,
  size,
  iconLeft,
  iconRight,
  isIcon,
  stretch,
  accent,
  isLoading,
  isMourning,
  renderLoader
}: BaseButtonProps): JSX.Element => {
  const cn = useMemo(() => classNames.bind(styles), [styles]);

  const tagName: ButtonTags = href ? 'a' : 'button';

  const baseName = isIcon ? 'icon_button' : 'button';

  const buttonClassNames = cn(
    baseName,
    `${ baseName }--${ type }`,
    `${ baseName }--${ size }`,
    {
      [`${ baseName }--stretch`]: stretch,
      [`${ baseName }--disabled`]: disabled,
      'button--with_icon_left': iconLeft,
      [`button--with_icon_left_${ size }`]: iconLeft,
      'button--with_icon_right': iconRight,
      [`button--with_icon_right_${ size }`]: iconRight,
      'button--loading': isLoading,
      [`button--accent_${ type }`]: accent && ['secondary', 'tertiary'].includes(type || ''),
      [`button--withLoader_${ size }`]: isLoading && renderLoader,
      'button--mourning': isMourning
    }
  );
  const targetAttr = href ? target : null;
  const relAttr = href ? rel : null;
  const roleAttr = href ? null : role;

  return React.createElement(
    tagName,
    {
      type: 'button',
      className: buttonClassNames,
      onClick,
      href: href || null,
      target: targetAttr,
      rel: relAttr,
      role: roleAttr,
      'aria-label': ariaLabel,
      'data-test-id': dataTestId,
      disabled: disabled || isLoading
    },
    <>
      { isLoading && renderLoader && renderLoader(size) }
      { iconLeft && <div className={ styles[`icon--${ size }`] }>{ iconLeft }</div> }
      { content }
      { iconRight && <div className={ styles[`icon--${ size }`] }>{ iconRight }</div> }
    </>
  );
};
